import { useState } from 'react';
import ToDoList from './ToDoList';
import './App.css';
import './index.css';

const days = ['Sun', 'Mon', 'Tue', 'Wed', 'Thur', 'Fri', 'Sat'];
const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'June', 'July', 'Aug', 'sept', 'Oct', 'Nov', 'Dec'];


let date = new Date();

function display(i) {
  if (i < 10) {
    i = "0" + i;
  }
  return i;
}
let hour = display(date.getHours());
let min = display(date.getMinutes());
let day = date.getDay();
let month = date.getMonth();
let d = date.getDate();

function App() {

  const [inputList, setInputList] = useState("");
  const [itemArr, setItemArr] = useState([]);

  const items = (event) => {
    setInputList(event.target.value)
  }

  const listOfItems = () => {
    setItemArr((oldItems) => {
      return [...oldItems, inputList]
    })
    setInputList("");
  }
  const deleteitem = (id) => {
    setItemArr((oldItems) => {
      return oldItems.filter((arrElem, index) => {
        return index !== id;
      })
    })
  }

  return (
    <>
      <div className="App">
        <div className="container">
          <br />
          <h1>To-Do-List</h1><br />
          <p className="time">{`${hour}:${min}`}</p>
          <p className="day">{`${days[day]},${months[month]}${d}`}</p>
          <input type="text" placeholder="add items"
            value={inputList} onChange={items} />
          <button onClick={listOfItems}>+</button>

          <ol>
            {/* <li>{inputList}</li> */}
            {itemArr.map((itemval, index) => {

              return <ToDoList
                key={index}
                id={index}
                text={itemval}
                onSelect={deleteitem}
              />

            })}
          </ol>
        </div>
      </div>
    </>
  );
}

export default App;
